@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700|Open+Sans:400,700&display=swap');
$color1: #292F31;
$color2: #BAB5C6;
$color3: #78787B;
$color4: #58B9CC;
$color5: #8450F8;

* {
  padding: 0;
  margin: 0;
}

a {
  position: relative;
  color: $color5;
  text-decoration: none;
  cursor: pointer;
}


.navlinks:before, .contentlinks:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: $color5;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.navlinks:hover:before, .contentlinks:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}


.active{
  font-weight: 800;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Montserrat';
}

body {
  font-family: 'Open Sans';
  background-color: $color1;
  // overflow-x: hidden;
}

.btn{
  text-align: center;
  padding: 0.5em 1em;
  background: transparent;
  border-radius: 4px;
  border: 1px solid $color4;
  color: $color4;
  outline: 0;
  width: auto;
  cursor: pointer;
  transition: 0.3s;
}

.btn:hover{
  background: $color4;
  color: $color1;
}

.btn-active{
  background: $color4;
  color: $color1;
}


html {
  body {
    
    header {
      width: 100vw;
      height: auto;
      position: fixed;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: $color1;
      border-bottom: 1px solid;
      z-index: 999;

      nav {
        width: 100%;
        padding: 0 5em;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        ul{
          li{
            display: inline-block;
            padding: 1.5em 1.8em;
            color:$color4;
            font-size: 0.9em;
          }
        }
        span{
          display: none;
          padding: 1em;
            color:$color5;
            font-size: 1.2em;
        }
        .drawer-nav{
          display: none;
        }
      }
    }
    .background{
      position: absolute;
      width: 100%;
      height: 1000px;
      background: url('./logo.svg');
      background-size: 80%;
      background-repeat: no-repeat;
      background-position: 25em 0em;
      opacity: 0.1;
    }
    main {
      
      section {
        max-width: 1000px;
        display: flex;
        -webkit-box-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        flex-direction: column;
        align-items: flex-start;
        padding: 150px 0;
        height: auto;
        margin: 0px auto;

        .flex-container {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          width: 100%;
          height: auto;

          .headers {
            p {
              color: $color2;
              font-size: 1.5em;
            }

            h1 {
              font-size: 4em;
              color: $color5;
              // width: 8em;
              line-height: 1.2em;
              margin: 0.2em 0;
            }

            h2 {
              font-size: 2em;
              color: $color3;
              // width: 8em;
              line-height: 1.2em;
            }
          }

          .hero_description {
            margin-top: 25px;
            height: auto;
            width: 30em;

            p {
              font-size: 1.1em;
              color: $color3;
              line-height: 1.5em;
              // width: 20em;
            }
          }

          .btn-contact {
            padding: 1.5em 3em;
            margin-top: 3em;
            min-width: 10em;
            width: 10em;
            font-weight: 700;
            font-size: 0.9em;
          }
        }

        .content {
          width: 100%;

          h3 {
            color: $color2;
            font-size: 2em
          }

          .about-flex-container {
            display: flex;
            flex-direction: row-reverse;
            flex-wrap: wrap-reverse;
            justify-content: space-between;
            align-items: flex-end;
            
            .overlay{
              margin: 3em;
              background-color: $color5;
              width: 16em;
              height: 15.9em;
              box-shadow: $color5;
              -webkit-box-shadow: 26px -26px 0px 0px $color5;
              -moz-box-shadow: 26px -26px 0px 0px $color5;
              box-shadow: 26px -26px 0px 0px $color5;
              transition: 0.3s;
              
              img {
                width: 16em;
                align-self: center;
                margin: 0;
                padding: 0;
                opacity: 0.7;
              }
            }
            .overlay:hover{
              background-color: transparent;
              box-shadow: $color5;
              -webkit-box-shadow: 22px -22px 0px 0px $color5;
              -moz-box-shadow: 22px -22px 0px 0px $color5;
              box-shadow: 22px -22px 0px 0px $color5;
              image{
                opacity: 1;
              }
            }

            .about-paragraphs {
              p {
                font-size: 1.2em;
                color: $color3;
                line-height: 1.5em;
                width: 30em;
              }
              ul {
                display: grid;
                grid-template-columns: repeat(2, auto);
                grid-template-rows: repeat(4, auto);
                grid-gap: 6px;
  
                .about_skills_items {
                  color: $color4
                }
              }
            }

            
          }
        }
        .experience{
          text-align: center;
          .experience-flex-container {
            text-align: left;
            display: flex;
            flex-direction: row;
            // height: 30em;
            justify-content: center;
            .experience-nav {
              width: 50%;
              padding: 2em;
              
              .experience-nav-items {
                display: block;
                width: 100%;
                margin-bottom: 1em;
                float: right;
              }
            }
  
            .experience-container {
              width: 50%;
              padding: 2em;
              height: auto;
  
              h4 {
                font-size: 1.2em;
                color: $color2;
  
                span {
                  color: $color5
                }
              }
  
              h5 {
                padding: 0.5em;
                font-size: 1em;
                color: $color3;
              }
  
              ul {
                list-style: none;
                padding-left: 30px;
  
                li::before {
                  content: "\2022";
                  color: $color4;
                  font-weight: bold;
                  display: inline-block;
                  width: 1em;
                  margin-left: -1em;
                }
  
                li {
                  font-size: 1.1em;
                  padding: 0.2em;
                  color: $color3;
                  line-height: 1.2em;
                }
              }
            }
          }
        }

        .works-flex-container{
          text-align: center;

          .project-odd,.project-even{
            position: relative;
            margin-bottom: 100px;
            height: 360px;
            .overlay{
              background-color: $color5;
              
            }
            .details{
              h4{
                color:$color5;
              }
              h3{
                
                margin:0.5em 0;
                font-size: 1.5em;
              }
              .description{
                width: auto;
                max-width: 30em;
                padding: 2em;
                background-color: rgba(52, 54, 54, 1);
                color:$color2;
                p{
                  font-size: 1em;
                  line-height: 1.5em;
                }
              }
              ul{
                margin-top:0.7em;
                li{
                  display: inline-block;
                  padding: 0.3em;
                  color:$color4;
                }
              }
              .links{
                margin-top:0.7em;
                a{
                  padding: 0.5em;
                  font-size: 1.3em;
                }
              }
            }
          }
          .project-odd{
            .overlay{
              position: absolute;
                left:0;
              $width:580px;
              $height:360px;
              width: $width;
              height:$height;
              
                img{
                  object-fit: cover;
                  object-position: 0 0;
                  opacity: 0.2;
                  width: $width;
                  height:$height;
                }
              
              
            }
            .details{
              position: absolute;
              top:0;
              right: 0;
              text-align: right;
              h3{
                max-width: 100%;
                margin-right: 0!important;
              }
            }
          }

          .project-even{
            .overlay{
              position: absolute;
              right:0;
              $width:580px;
              $height:360px;
              width: $width;
              height:$height;
              
              img{
                object-fit: cover;
                object-position: 0 0;
                opacity: 0.2;
                width: $width;
                height:$height;
            }
            }
            
            .details{
              position: absolute;
              top:0;
              left: 0;
              text-align: left;
              h3{
                max-width: 70%;
                margin-left: 0!important;
              }
            }
            
          }
        }
          .contact-flex-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            height: 30em;
            h3 {
              font-size: 1.2em;
              color: $color2;
              font-size: 2em
            }

            h1 {
              font-size: 3.5em;
              color: $color5;
            }

            p {
              font-size: 1.2em;
              color: $color3;
              line-height: 1.5em;
              width: 30em;
              text-align: center;
            }
          }
        }
      }
    

    footer {
      padding: 2em;
      background: #0e0e0e;
      height: auto;
      text-align: center;
      color: $color3;

      .footer-nav {
        display: flex;
        flex-direction: row;
        justify-content: center;
        padding: 1em;
        width: auto;
        margin: 0 auto;

        .footer-nav-items {
          padding: 0.2em 0.9em;
        }
      }
    }

    .social-tab {
      height: auto;
      position: fixed;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-color: #0e0e0e;
      opacity: 0.5;

      .social-tab-nav {
        .social-tab-nav-items {
          padding: 1em;
          color: $color2;
          font-size: 1em;
        }
      }
    }

    .email-tab {
      height: auto;
      position: fixed;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      background-color: #0e0e0e;
      opacity: 0.5;

      a {
        padding: 1em;
        font-size: 1em;
        writing-mode: vertical-rl;

        text-decoration: none;
        font-family: 'Montserrat';
        letter-spacing: 0.1em;
      }
    }
    
  }
}








