          .experience-flex-container {
            text-align: left;
            display: flex;
            flex-direction: row;
            // height: 30em;
            flex-wrap: nowrap;
            justify-content: center;
            .experience-nav {
              width: 100%;
              padding: 2em;
              
              .experience-nav-items {
                padding: 1em;
                display: inline-block;
                width: 100%;
                margin-bottom: 1em;
                // float: right;
              }
            }
  
            .experience-container {
              width: 100%;
              padding: 2em;
              height: auto;
  
              h4 {
                font-size: 1.2em;
                color: $color2;
  
                span {
                  color: $color5
                }
              }
  
              h5 {
                padding: 0.5em;
                font-size: 1em;
                color: $color3;
              }
  
              ul {
                list-style: none;
                padding-left: 30px;
  
                li::before {
                  content: "\2022";
                  color: $color4;
                  font-weight: bold;
                  display: inline-block;
                  width: 1em;
                  margin-left: -1em;
                }
  
                li {
                  font-size: 1.2em;
                  padding: 0.2em;
                  color: $color3;
                  line-height: 1.2em;
                }
              }
            }
          }
        }

        .works-flex-container{
          // text-align: center;

          .project-odd,.project-even{
            position: relative;
            margin-bottom: 100px;
            height: 360px;
            .overlay{
              background-color: $color5;
            }
            .details{
              padding: 2em 0;
              h4{
                color:$color5;
              }
              h3{
                margin:0.5em auto;
                font-size: 1.5em;
              }
              .description{
                width: auto;
                max-width: 30em;
                padding: 1em;
                background-color: rgba(52, 54, 54, 1);
                color:$color2;
                p{
                  font-size: 1.2em;
                  line-height: 1.5em;
                }
              }
              ul{
                margin-top:0.7em;
                li{
                  display: inline-block;
                  padding: 0.3em;
                  color:$color4;
                }
              }
              .links{
                margin-top:0.7em;
                a{
                  padding: 0.5em;
                  font-size: 1.3em;
                }
              }
            }
          }
          .project-odd{
            .overlay{
              position: absolute;
              left:0;
              $width:400px;
              $height:360px;
              width: $width;
              height:$height;
              img{
                object-fit: cover;
                object-position: 0 0;
                opacity: 0.1;
                width: $width;
                height:$height;
              }
            }
            .details{
              position: absolute;
              top:0;
              right: 0;
              text-align: right;
              h3{
                width: 70%;
                margin-right: 0!important;
              }
            }
          }

          .project-even{
            .overlay{
              position: absolute;
              right:0;
              $width:400px;
              $height:360px;
              width: $width;
              height:$height;
              img{
                object-fit: cover;
                object-position: 0 0;
                opacity: 0.1;
                width: $width;
                height:$height;
            }
            }
            
            .details{
              position: absolute;
              top:0;
              left: 0;
              text-align: left;
              h3{
                width: 70%;
                margin-left: 0!important;
              }
            }
          }
        }
        

        

          .contact-flex-container {
            display: flex;
            flex-direction: column;
            align-items: center;
            height: 30em;
            h3 {
              
              color: $color2;
              font-size: 1.5em
            }

            h1 {
              font-size: 3.5em;
              color: $color5;
            }

            p {
              font-size: 1.2em;
              color: $color3;
              line-height: 1.5em;
              width: 30em;
              text-align: center;
            }
          }
        }
      }
    

    footer {
      padding: 2em;
      background: #0e0e0e;
      height: auto;
      text-align: center;
      color: $color3;

      .footer-nav {
        display: inline-flex;
        flex-direction: row;
        justify-content: space-evenly;
        padding: 0.5em;

        .footer-nav-items {
          padding: 0.2em 0.9em;
        }
      }
    }

    .social-tab {
      display: none;
      height: auto;
      position: fixed;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-color: #0e0e0e;
      opacity: 0.5;

      .social-tab-nav {
        .social-tab-nav-items {
          padding: 1em;
          color: $color2;
          font-size: 1em;
        }
      }
    }

    .email-tab {
      display: none;
      height: auto;
      position: fixed;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      background-color: #0e0e0e;
      opacity: 0.5;

      a {
        padding: 1em;
        font-size: 1em;
        writing-mode: vertical-rl;

        text-decoration: none;
        font-family: 'Montserrat';
        letter-spacing: 0.1em;
      }
    }
  }
}
}




.loader{
  position: absolute;
  left:50%;
  top:50%;
  animation: animationFrames ease-in-out 5s;
  animation-iteration-count: 1;
  transform: translateY(-50%) translateX(-50%) scaleX(0.00) scaleY(0.00) ;
  -webkit-animation: animationFrames ease-in-out 5s;
  -webkit-animation-iteration-count: 1;
  -webkit-transform: translateY(-50%) translateX(-50%) scaleX(0.00) scaleY(0.00) ;
  -moz-animation: animationFrames ease-in-out 5s;
  -moz-animation-iteration-count: 1;
  -moz-transform: translateY(-50%) translateX(-50%) scaleX(0.00) scaleY(0.00) ;
  -o-animation: animationFrames ease-in-out 5s;
  -o-animation-iteration-count: 1;
  -o-transform: translateY(-50%) translateX(-50%) scaleX(0.00) scaleY(0.00) ;
  -ms-animation: animationFrames ease-in-out 5s;
  -ms-animation-iteration-count: 1;
  -ms-transform: translateY(-50%) translateX(-50%) scaleX(0.00) scaleY(0.00) ;
}

@keyframes animationFrames{
  0% {
    opacity:1;
    transform: translateY(-50%) translateX(-50%) scaleX(0.00) scaleY(0.00) ;
  }
  8% {
    opacity:0.9396304265076563;
    transform: translateY(-50%) translateX(-50%) scaleX(1.00) scaleY(1.00) ;
  }
  21% {
    opacity:0.8;
    transform: translateY(-50%) translateX(-50%) scaleX(0.80) scaleY(0.80) ;
  }
  36% {
    opacity:1;
    transform: translateY(-50%) translateX(-50%) scaleX(1.00) scaleY(1.00) ;
  }
  49% {
    opacity:0.8;
    transform: translateY(-50%) translateX(-50%) scaleX(0.80) scaleY(0.80) ;
  }
  64% {
    opacity:1;
    transform: translateY(-50%) translateX(-50%) scaleX(1.00) scaleY(1.00) ;
  }
  79% {
    opacity:0.8;
    transform: translateY(-50%) translateX(-50%) scaleX(0.80) scaleY(0.80) ;
  }
  93% {
    opacity:1;
    transform: translateY(-50%) translateX(-50%) scaleX(1.00) scaleY(1.00) ;
  }
  100% {
    opacity:1;
    transform: translateY(-50%) translateX(-50%) scaleX(0.00) scaleY(0.00) ;
  }
}

